import React from 'react';
import Navbar from './components/Navbar';
import './App.css';
import Home from './components/pages/Home';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Build from './components/pages/Build';
import Runes from './components/pages/Runes';
import Winrate from './components/pages/Winrate';
import Skins from './components/pages/Skins';
import ScrollToTop from './components/Scroll'
import Abilities from './components/pages/Abilities'

function App() {
  return (
    <>
      <Router>
        <ScrollToTop />
          <Navbar />
          <Switch>
            <Route path='/' exact component={Home} />
            <Route path='/build' exact component={Build} />
            <Route path='/runes' exact component={Runes} />
            <Route path='/winrate' exact component={Winrate} />
            <Route path='/skins' exact component={Skins} />
            <Route path='/abilities' exact component={Abilities} />
          </Switch>
        </Router>
    </>
  );
}

export default App;
