import React from 'react';
import './Cards.css';
import CardItemVideo from './CardItemVideo';

function CardAbilities() {
    return (
      <div className='cards' >
        <h1>ABILITIES</h1>
        <div className='cards__container2'>
          <div className='cards__wrapper'>
          <ul className='cards__items'>
              <CardItemVideo
                src='/videos/video-2.webm'
                text1="WAY OF THE WANDERER"
                text2="Yasuo's Critical Strike Chance is increased. Additionally, Yasuo builds toward a shield whenever he is moving. The shield triggers when he takes damage from a champion or monster."
                label='PASSIVE'
                imgsrc='/images/passive.png'
              />
            </ul>
            <ul className='cards__items'>
              <CardItemVideo
                src='/videos/video-3.webm'
                text1="STEEL TEMPEST"
                text2="Thrusts forward, damaging all enemies in a line. On hit, grants a stack of Gathering Storm for a few seconds. At 2 stacks, Steel Tempest fires a whirlwind that knocks Airborne. Steel Tempest is treated as a basic attack and scales with all the same things."
                label='Q'
                imgsrc='/images/Q.png'
              />
            </ul>
            <ul className='cards__items'>
              <CardItemVideo
                src='/videos/video-4.webm'
                text1="WIND WALL"
                text2="Creates a moving wall that blocks all enemy projectiles for 4 seconds."
                label='W'
                imgsrc='/images/W.png'
              />
            </ul>
            <ul className='cards__items'>
              <CardItemVideo
                src='/videos/video-5.webm'
                text1="SWEEPING BLADE"
                text2="Dashes through target enemy, dealing magic damage. Each cast increases your next dash's base Damage, up to a max amount. Cannot be re-cast on the same enemy for a few seconds. If Steel Tempest is cast while dashing, it will strike as a circle."
                label='E'
                imgsrc='/images/E.png'
              />
            </ul>
            <ul className='cards__items'>
              <CardItemVideo
                src='/videos/video-6.webm'
                text1="LAST BREATH"
                text2="Blinks to an Airborne enemy champion, dealing physical damage and holding all Airborne enemies in the area in the air. Grants maximum Flow but resets all stacks of Gathering Storm. For a moderate time afterwards, Yasuo's critical strikes gain significant Bonus Armor Penetration."
                label='R'
                imgsrc='/images/R.png'
              />
            </ul>
          </div>
        </div>
      </div>
    );
  }
  
  export default CardAbilities;