import React from 'react';
import { Link } from 'react-router-dom';

function CardItemVideo(props) {
  return (
    <>
      <li className='cards__item'>
        <Link className='cards__item__link2 skillpointer' to={props.path}>
          <figure className='cards__item__pic-wrap2 cards__video' data-category={props.label}>
            <video
              className='cards__item__img'
              alt='Travel'
              src={props.src}
              autoPlay loop muted
            />
          </figure>
          <div className='cards__item__info'>
            <div className='skillsimg'>
            <img src={props.imgsrc} alt='yeah' width="35" height="35"  />
            </div>
            <h5 className='cards__item__text1'>{props.text1}</h5>
            <p className='cards__item__text2'>{props.text2}</p>
          </div>
        </Link>
      </li>
    </>
  );
}

export default CardItemVideo;
